'use client'

import { useState, useEffect } from 'react'

interface Member {
  id: number
  first_name: string
  last_name: string
  role: string
  email: string
  phone: string
}

const roleLabels: Record<string, string> = {
  PASTOR: 'Pasteur',
  ELDER: 'Ancien',
  DEACON: 'Diacre',
  MEMBER: 'Membre',
  VISITOR: 'Visiteur',
}

const roleColors: Record<string, string> = {
  PASTOR: 'bg-purple-100 text-purple-800',
  ELDER: 'bg-blue-100 text-blue-800',
  DEACON: 'bg-green-100 text-green-800',
  MEMBER: 'bg-gray-100 text-gray-800',
  VISITOR: 'bg-yellow-100 text-yellow-800',
}

export default function Home() {
  const [members, setMembers] = useState<Member[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    fetch('http://localhost:8000/api/members/')
      .then(res => {
        if (!res.ok) throw new Error(`Erreur HTTP: ${res.status}`)
        return res.json()
      })
      .then(data => {
        setMembers(data)
        setLoading(false)
      })
      .catch(err => {
        setError(err.message)
        setLoading(false)
      })
  }, [])

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <header className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            Annuaire des Membres - √âglise de la Grande Gr√¢ce de D√©fal√©
          </h1>
          <p className="text-lg text-gray-600 mb-6">
            {members.length} membres dans notre communaut√©
          </p>
          <div className="flex justify-center gap-4">
          <a href="http://localhost:8000/admin" target="_blank" rel="noopener noreferrer"
           className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition">
            Admin Django
           </a>
            <a href="http://localhost:8000/api/members/" target="_blank" rel="noopener noreferrer"
              className="bg-green-600 text-white px-6 py-2 rounded-lg hover:bg-green-700 transition"
            >
              Voir l'API
            </a>
          </div>
        </header>

        {/* Stats */}
        <div className="grid grid-cols-2 md:grid-cols-5 gap-4 mb-8">
          {['PASTOR', 'ELDER', 'DEACON', 'MEMBER', 'VISITOR'].map(role => {
            const count = members.filter(m => m.role === role).length
            return (
              <div key={role} className="bg-white p-4 rounded-lg shadow text-center">
                <div className={`inline-block px-3 py-1 rounded-full text-sm font-semibold ${roleColors[role]} mb-2`}>
                  {roleLabels[role]}
                </div>
                <div className="text-2xl font-bold">{count}</div>
              </div>
            )
          })}
        </div>

        {/* Table des membres */}
        <div className="bg-white rounded-xl shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-2xl font-bold text-gray-800">Liste des Membres</h2>
          </div>

          {loading ? (
            <div className="p-8 text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
              <p className="mt-4 text-gray-600">Chargement des membres depuis Django...</p>
            </div>
          ) : error ? (
            <div className="p-8 text-center bg-red-50">
              <p className="text-red-600 font-semibold">Erreur de connexion : {error}</p>
              <p className="mt-2 text-gray-600">V√©rifiez que Django est d√©marr√© sur le port 8000</p>
            </div>
          ) : members.length === 0 ? (
            <div className="p-8 text-center">
              <p className="text-gray-600">Aucun membre trouv√©.</p>
              <a href="http://localhost:8000/admin/members/member/add/" 
                target="_blank" rel="noopener noreferrer"
                className="text-blue-600 hover:underline block mt-2"
              >
                Ajoutez des membres dans l'admin Django
              </a>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Nom</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">R√¥le</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Email</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">T√©l√©phone</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {members.map((member) => (
                    <tr key={member.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="font-medium text-gray-900">
                          {member.first_name} {member.last_name}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-3 py-1 rounded-full text-xs font-semibold ${roleColors[member.role]}`}>
                          {roleLabels[member.role]}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-gray-600">
                        {member.email}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-gray-600">
                        {member.phone || 'Non renseign√©'}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>

        {/* Info syst√®me */}
        <div className="mt-8 p-6 bg-blue-50 rounded-xl">
          <h3 className="text-lg font-semibold text-blue-800 mb-3">√âtat du syst√®me</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <p className="text-sm">
                <span className="inline-block w-3 h-3 rounded-full bg-green-500 mr-2"></span>
                <strong>Next.js</strong> : localhost:3000
              </p>
              <p className="text-sm">
                <span className="inline-block w-3 h-3 rounded-full bg-green-500 mr-2"></span>
                <strong>Django</strong> : localhost:8000
              </p>
            </div>
            <div>
              <p className="text-sm">
                <a href="http://localhost:8000/api/members/" target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">
                  üîó API JSON des membres
                </a>
              </p>
              <p className="text-sm">
                <a href="http://localhost:8000/admin" target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">
                  üîó Panneau d'administration
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}